:root {
  --location-title-color1: #f0d1a5;
  --location-title-color2: #c2e297;
  --schedule-color: #ffa5ba;
  --location-card-text: #907e82;
  --button-background-color: #d41443;
  --button-text-color: #d41443;
  --color-white: #fff;
  --franchise-background-hover-color: #ecebeb;
}

.location-cards {
  width: 100%;
  flex-flow: column wrap;
  align-content: space-around;
  justify-content: space-evenly;
  gap: 32px;
  padding-left: 16px;
  padding-right: 16px;
  display: flex;
}

.location-card-info {
  letter-spacing: .48px;
  background: var(--color-white);
  border-radius: 24px;
  flex-direction: column;
  gap: 32px;
  padding: 40px 32px;
  font-family: DM Sans;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 18px;
  display: flex;
  box-shadow: 0 8px 30px rgba(212, 20, 67, .1);
}

.location-info-title {
  width: 75.103px;
  height: 28px;
  background-color: var(--location-title-color1);
  color: var(--color-white);
  text-transform: uppercase;
  border-radius: 6px;
  justify-content: center;
  align-items: center;
  animation: animate30 2s 10ms infinite;
  display: flex;
}

@keyframes animate30 {
  0% {
    transform: rotate(0deg);
  }

  25% {
    transform: rotate(-15deg);
  }

  50% {
    transform: rotate(15deg);
  }

  75% {
    transform: rotate(-15deg);
  }

  100% {
    transform: rotate(0deg);
  }
}

.title1 {
  width: 138px;
  height: 28px;
  background: var(--location-title-color2);
}

.location-card-info-contact {
  flex-direction: column;
  display: flex;
}

.location-card-title-description {
  width: 208px;
  height: 36px;
  color: var(--location-card-text);
}

.location-card-schedule {
  color: var(--schedule-color);
}

.location-card-info-contact a {
  color: var(--location-card-text);
}

.location-card-info-contact a:hover {
  color: var(--button-text-color);
}

.modal-btns {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10px;
  padding-top: 61px;
  display: flex;
}

.modal-btns .button {
  color: var(--color-white);
  cursor: pointer;
  border: none;
  border-radius: 22px;
  font-family: DM Sans;
  font-size: 16px;
  font-weight: 700;
}

.modal-btns .our-location-btn {
  width: 198px;
  height: 44px;
  background-color: var(--button-background-color);
  border-color: var(--button-background-color);
}

.modal-btns .franchise-btn {
  width: 168px;
  height: 44px;
  background-color: var(--color-white);
  border-color: var(--color-white);
  color: var(--button-text-color);
}

.modal-btns .franchise-btn:hover {
  background-color: var(--franchise-background-hover-color);
}

@media screen and (min-width: 768px) {
  .location-cards {
    flex-direction: row;
    display: flex;
  }

  .location-card-info {
    letter-spacing: .56px;
    padding: 40px 32px;
    font-size: 14px;
    line-height: 186%;
  }

  .modal-btns {
    flex-direction: row;
