  top: 25px;
}

.customer-review-text-block {
  width: 968px;
  height: 138px;
  background: #fafafa;
  flex-direction: row;
  display: flex;
  position: relative;
}

.customer-review-text-block svg {
  display: flex;
  position: relative;
  top: 40px;
  left: 0;
}

.customer-review-text-block p {
  text-underline-offset: .2em;
  border-width: 0 0 10px;
  text-decoration: underline #e1e1e1;
  text-decoration-thickness: 2.2px;
}

.inactive {
  display: none;
}

.testim {
  width: 100%;
  margin-top: 300px;
  margin-bottom: -120px;
  transform: translateY(-50%);
}

.testim .arrow {
  color: #eee;
  cursor: pointer;
  -o-transition: all .3s ease-in-out;
  z-index: 22222222;
  padding: 5px;
  font-size: 2em;
  transition: all .3s ease-in-out;
  display: block;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.testim .arrow:before {
  cursor: pointer;
}

.testim .arrow:hover {
  color: #ea830e;
}

.testim .arrow.left {
  left: 10px;
}

.testim .arrow.right {
  right: 10px;
}

.testim .dots {
  text-align: center;
  width: 100%;
  z-index: 3333;
  height: 12px;
  justify-content: center;
  display: flex;
  position: absolute;
  bottom: 30px;
  left: 0;
}

.testim .dots .dot {
  width: 15px;
  height: 15px;
  cursor: pointer;
  -o-transition: all .5s ease-in-out;
  background-color: #d9d9d9;
  border-radius: 50%;
  margin: 0 10px;
  list-style-type: none;
  transition: all .5s ease-in-out;
  display: inline-block;
  position: relative;
}

.testim .dots .dot.active, .testim .dots .dot:hover {
  background: url("Frame1.90988fea.svg") 50% 0 / 15px no-repeat;
}

.testim .dots .dot.active {
  -ms-animation: testim-scale .5s ease-in-out forwards;
  animation: testim-scale .5s ease-in-out forwards;
}

.testim .cont {
  justify-content: center;
  display: flex;
  position: relative;
  overflow: hidden;
}

.testim .cont > div {
  text-align: center;
  opacity: 0;
  padding: 0 0 70px;
  position: absolute;
  top: 0;
  left: 0;
}

.testim .cont > div.inactive {
  opacity: 1;
}

.testim .cont > div.active {
  opacity: 1;
  position: relative;
}

.testim .cont div .img img {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  margin: auto;
  display: block;
}

.testim .cont div h2 {
  color: #d31342;
  margin: 15px 0;
  font-size: 1em;
}

.testim .cont div p {
  color: #000;
  width: 80%;
  margin: auto;
  font-size: 1.15em;
}

.testim .cont div.active .img img {
  -ms-animation: testim-show .5s ease-in-out forwards;
  animation: testim-show .5s ease-in-out forwards;
}

.testim .cont div.active h2 {
  -ms-animation: testim-content-in .4s ease-in-out forwards;
  animation: testim-content-in .4s ease-in-out forwards;
}

.testim .cont div.active p {
  -ms-animation: testim-content-in .5s ease-in-out forwards;
  animation: testim-content-in .5s ease-in-out forwards;
}

.testim .cont div.inactive .img img {
  -ms-animation: testim-hide .5s ease-in-out forwards;
  animation: testim-hide .5s ease-in-out forwards;
}

.testim .cont div.inactive h2 {
  -ms-animation: testim-content-out .4s ease-in-out forwards;
  animation: testim-content-out .4s ease-in-out forwards;
}

.testim .cont div.inactive p {
  -ms-animation: testim-content-out .5s ease-in-out forwards;
  animation: testim-content-out .5s ease-in-out forwards;
}

@-webkit-keyframes testim-scale {
  0% {
    box-shadow: 0 0 #eee;
  }

  35% {
    box-shadow: 0 0 10px 5px #eee;
  }

  70% {
    box-shadow: 0 0 10px 5px #d31342;
  }

  100% {
    box-shadow: 0 0 #d31342;
  }
}

@-moz-keyframes testim-scale {
  0% {
    box-shadow: 0 0 #eee;
  }

  35% {
    box-shadow: 0 0 10px 5px #eee;
  }

  70% {
    box-shadow: 0 0 10px 5px #d31342;
  }

  100% {
    box-shadow: 0 0 #d31342;
  }
}



@-o-keyframes testim-scale {
  0% {
    -o-box-shadow: 0px 0px 0px 0px #eee;
    box-shadow: 0 0 #eee;
  }

  35% {
    -o-box-shadow: 0px 0px 10px 5px #eee;
    box-shadow: 0 0 10px 5px #eee;
  }

  70% {
    -o-box-shadow: 0px 0px 10px 5px #d31342;
    box-shadow: 0 0 10px 5px #d31342;
  }

  100% {
    -o-box-shadow: 0px 0px 0px 0px #d31342;
    box-shadow: 0 0 #d31342;
  }
}

@keyframes testim-scale {
  0% {
    box-shadow: 0 0 #eee;
  }

  35% {
    box-shadow: 0 0 10px 5px #eee;
  }

  70% {
    box-shadow: 0 0 10px 5px #d31342;
  }

  100% {
    box-shadow: 0 0 #d31342;
  }
}

@keyframes testim-content-in {
  from {
    opacity: 0;
    transform: translateY(100%);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes testim-content-out {
  from {
    opacity: 1;
    transform: translateY(0);
  }

  to {
    opacity: 0;
    transform: translateY(-100%);
  }
}

@keyframes testim-show {
  from {
    opacity: 0;
    transform: scale(0);
  }

  to {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes testim-hide {
  from {
    opacity: 1;
    transform: scale(1);
  }

  to {
    opacity: 0;
    transform: scale(0);
  }
}

